//
// Card
//

.#{$css-prefix}card {
  display: block;
  border-radius: get-card(border-radius);
  min-width: 0;
  position: relative;
  text-decoration: none;

  @include elevation(2);

  & > * {
    &:first-child {
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }

    &:last-child {
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;
    }
  }

  &--raised {
    @include elevation(3);
  }

  &--tile {
    border-radius: 0;
  }

  &--flat {
    @include elevation(0);
  }

  &--hover {
    cursor: pointer;
    transition: all .4s cubic-bezier(.25, .8, .25, 1);
    transition-property: box-shadow;

    &:hover {
      @include elevation(8);
    }
  }

  &__title {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    padding: 16px;

    &--primary {
      padding-top: 24px;
    }
  }

  &__text {
    padding: 16px;
    width: 100%;
  }

  &__media {
    display: flex;
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
    }

    &__background {
      border-radius: inherit;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    &__content {
      flex: 1;
      position: relative;
    }
  }

  &__actions {
    align-items: center;
    display: flex;
    padding: 8px 4px;

    & > * {
      margin: 0 4px;
    }
  }
}