//
// Font Size
//


$html-base-font-size: 16px;
$html-base-font-size-per: ($html-base-font-size / 16px) * 100%;

@function get-rem($ammount: 1) {
  @return $html-base-font-size * $ammount;
}

@function get-font-size($key, $unit: null) {
  $value: map-get($font-size, $key);
  @if $unit != null {
    @return #{nounit($value)}#{$unit};
  } @else {
    @return $value;
  }
}

$font-size: ();
$font-size: map-set($font-size, base   , get-rem());
$font-size: map-set($font-size, xs     , get-rem(.625));
$font-size: map-set($font-size, sm     , get-rem(.75));
$font-size: map-set($font-size, md     , get-rem());
$font-size: map-set($font-size, lg     , get-rem(1.125));
// $font-size: map-set($font-size, xl     , get-rem(1.15));
$font-size: map-set($font-size, xl    , get-rem(1.25));
$font-size: map-set($font-size, xxl   , get-rem(1.6));
$font-size: map-set($font-size, xxxl  , get-rem(2));
$font-size: map-set($font-size, xxxxl , get-rem(2.8125));
$font-size: map-set($font-size, xxxxxl, get-rem(7));
