@font-face {
  font-family: "dd-icon";
  src: url('../font/dd-icon/dd-icon.eot');
  src: url('../font/dd-icon/dd-icon.eot?#iefix') format('eot'),
    url('../font/dd-icon/dd-icon.woff2') format('woff2'),
    url('../font/dd-icon/dd-icon.woff') format('woff'),
    url('../font/dd-icon/dd-icon.ttf') format('truetype'),
    url('../font/dd-icon/dd-icon.svg#dd-icon') format('svg');
}

@mixin dd-icon-styles {
  font-family: "dd-icon";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
  text-decoration: none;
  text-transform: none;
}

%dd-icon {
  @include dd-icon-styles;
}

