@mixin dd-icon($filename, $insert: before, $extend: true) {
  &:#{$insert} {
    @if $extend {
      @extend %dd-icon;
    } @else {
      @include dd-icon-styles;
    }
    content: dd-icon-char($filename);
  }
}

